import React, { useEffect, useState } from "react";
import "./game.css";

function Game() {
  const [Player1, setPlayer1] = useState(false);
  const [Player2, setPlayer2] = useState(false);
  const [Winner, setWinner] = useState("");
  const [scorePlayer1, setScorePlayer1] = useState(0);
  const [scorePlayer2, setScorePlayer2] = useState(0);
  const [boardStates, setBoardStates] = useState([]);
  const [clickedBoxes, setClickedBoxes] = useState(0);
  const [easyMode, setEasyMode] = useState(false);
  const [mediumMode, setMediumMode] = useState(true);

  function firstPlayer() {
    var first = Math.floor(Math.random() * 2) + 1;
    if (first === 1) {
      setPlayer1(true);
      setPlayer2(false);
    }
    if (first === 2) {
      setPlayer1(false);
      setPlayer2(true);
    }
  }
  function addMarkOnBoard(value) {
    if (Player1 === true && Winner !== "Player2") {
      setBoardStates({ ...boardStates, [value]: "X" });
      setPlayer1(false);
      setPlayer2(true);
    }
    if (Player2 === true && Winner !== "Player1") {
      setBoardStates({ ...boardStates, [value]: "0" });
      setPlayer1(true);
      setPlayer2(false);
    }
    setClickedBoxes(clickedBoxes + 1);
    console.log(boardStates);
  }

  function AImove() {
    if (Player2 === true && Winner !== "Player1") {
      var emptyChoices = [];
      for (var i = 0; i < 9; i++) {
        if (!boardStates[i]) emptyChoices.push(i);
      }
      var move = Math.floor(Math.random() * emptyChoices.length);
      addMarkOnBoard(emptyChoices[move]);
    }
  }

  function AImoveMedium() {
    console.log(boardStates[2]);
    if (Player2 === true && Winner !== "Player1") {
      var emptyChoices = [];
      for (var i = 0; i < 9; i++) {
        if (!boardStates[i]) emptyChoices.push(i);
      }

      const rows = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6],
      ];

      for (let i = 0; i < rows.length; i++) {
        const [a, b, c] = rows[i];
        console.log("intra1");

        if (boardStates[a] === "0" && boardStates[b] === "0") {
          for (let j = 0; j < emptyChoices.length; j++) {
            if (emptyChoices[j] === c) {
              console.log("intra3win");
              return addMarkOnBoard(c);
            }
          }
        }
        if (boardStates[a] === "0" && boardStates[c] === "0") {
          for (let j = 0; j < emptyChoices.length; j++) {
            if (emptyChoices[j] === b) {
              console.log("intra3win");
              return addMarkOnBoard(b);
            }
          }
        }
        if (boardStates[b] === "0" && boardStates[c] === "0") {
          for (let j = 0; j < emptyChoices.length; j++) {
            if (emptyChoices[j] === a) {
              console.log("intra3win");
              return addMarkOnBoard(a);
            }
          }
        }
      }
      for (let i = 0; i < rows.length; i++) {
        const [a, b, c] = rows[i];
        console.log("intra1");

        if (boardStates[a] === "X" && boardStates[b] === "X") {
          console.log("intra");
          for (let j = 0; j < emptyChoices.length; j++) {
            if (emptyChoices[j] === c) {
              console.log("intra3");
              return addMarkOnBoard(c);
            }
          }
        }
        if (boardStates[b] === "X" && boardStates[c] === "X") {
          console.log("intra");
          for (let j = 0; j < emptyChoices.length; j++) {
            if (emptyChoices[j] === a) {
              console.log("intra3");
              return addMarkOnBoard(a);
            }
          }
        }
        if (boardStates[a] === "X" && boardStates[c] === "X") {
          console.log("intra");
          for (let j = 0; j < emptyChoices.length; j++) {
            if (emptyChoices[j] === b) {
              console.log("intra3");
              return addMarkOnBoard(b);
            }
          }
        }
      }
      var move = Math.floor(Math.random() * emptyChoices.length);
      return addMarkOnBoard(emptyChoices[move]);
    }
  }

  function checkWinner(BoardState) {
    const rows = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6],
    ];
    for (let i = 0; i < rows.length; i++) {
      const [a, b, c] = rows[i];

      // Check if the game board contains winning combination
      if (
        BoardState[a] &&
        BoardState[a] === BoardState[b] &&
        BoardState[a] === BoardState[c]
      ) {
        // Return the winner ('x' or 'o')
        if (Player1 === true) {
          setWinner("Player2");
          setClickedBoxes(0);
          setScorePlayer2(scorePlayer2 + 1);
        }
        if (Player2 === true) {
          setWinner("Player1");
          setClickedBoxes(0);
          setScorePlayer1(scorePlayer1 + 1);
        }

        return setBoardStates([]);
      }
    }
    if (clickedBoxes === 9) {
      setTimeout(() => {
        setClickedBoxes(0);
        return setBoardStates([]);
      }, 2000);
    }
    // Otherwise do nothing
    return null;
  }

  useEffect(() => {
    if (Player1 === false && Player2 === false) firstPlayer();
    if (easyMode && Winner === "") AImove();
    if (mediumMode && Winner === "") AImoveMedium();
    if (Winner !== "") {
      setTimeout(() => {
        setWinner("");
      }, 2000);
    }

    checkWinner(boardStates);

    console.log(clickedBoxes);

    console.log(boardStates);
  }, [boardStates]);
  return (
    <div className="game">
      <h3>Score</h3>
      <h3>
        <span> Player One {scorePlayer1}</span> - {scorePlayer2} Player Two
      </h3>
      <button
        onClick={() => {
          setScorePlayer1(0);
          setScorePlayer2(0);
          setBoardStates([]);
          setClickedBoxes(0);
        }}
      >
        Restart
      </button>
      <div className="checkIn">
        <input
          type="checkbox"
          defaultChecked={easyMode}
          checked={easyMode}
          onChange={() => {
            setMediumMode(false);
            setEasyMode(!easyMode);
          }}
        />{" "}
        <label> Easy AI</label>
      </div>
      <div className="checkIn">
        <input
          type="checkbox"
          checked={mediumMode}
          defaultChecked={mediumMode}
          onChange={() => {
            setMediumMode(!mediumMode);
            setEasyMode(false);
          }}
        />{" "}
        <label> Medium AI</label>
      </div>
      {Winner && (
        <div className="WinnerAnouncement">{Winner + " has won the game."}</div>
      )}
      {clickedBoxes >= 9 && (
        <div className="WinnerAnouncement">It's a tie !</div>
      )}
      <ul>
        <h1>Player 1 : X</h1>
        <h1>Player 2 : 0</h1>
        <li
          onClick={() => {
            if (!boardStates[0]) addMarkOnBoard(0);
          }}
        >
          {boardStates[0] ? (
            <p className="taken">{boardStates[0]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[1]) addMarkOnBoard(1);
          }}
        >
          {boardStates[1] ? (
            <p className="taken">{boardStates[1]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[2]) addMarkOnBoard(2);
          }}
        >
          {boardStates[2] ? (
            <p className="taken">{boardStates[2]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[3]) addMarkOnBoard(3);
          }}
        >
          {boardStates[3] ? (
            <p className="taken">{boardStates[3]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[4]) addMarkOnBoard(4);
          }}
        >
          {boardStates[4] ? (
            <p className="taken">{boardStates[4]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[5]) addMarkOnBoard(5);
          }}
        >
          {boardStates[5] ? (
            <p className="taken">{boardStates[5]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[6]) addMarkOnBoard(6);
          }}
        >
          {boardStates[6] ? (
            <p className="taken">{boardStates[6]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[7]) addMarkOnBoard(7);
          }}
        >
          {boardStates[7] ? (
            <p className="taken">{boardStates[7]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
        <li
          onClick={() => {
            if (!boardStates[8]) addMarkOnBoard(8);
          }}
        >
          {boardStates[8] ? (
            <p className="taken">{boardStates[8]}</p>
          ) : (
            (Player1 && <p>X</p>) || (Player2 && <p>0</p>)
          )}
        </li>
      </ul>
      <h2>{Player1 ? "Player one Turn" : <span>Player Two Turn</span>}</h2>
    </div>
  );
}

export default Game;
